import React, { useEffect, useState } from "react";

import classes from "./Nav.module.css";
import navItems from "./NavItems";
import ryulabIcon from './Img/ryulabIcon.png';

const Nav = (props) => {
  const [navs, setNavs] = useState(navItems);

  //
  useEffect(() => {
    //1(order:1)   (isSelected:true)
    setNavs((prev) => {
      const initNav = [...prev];
      initNav.map((map, i) => {
        if (map.order === 1) {
          map.isSelected = true;
        }
      });
      return initNav;
    });
    const selectedNav = navs.filter((filter) => filter.isSelected)[0];
    props.onSelect(selectedNav.target);
  }, []);

  //  
  const clickHandler = (nav) => {
    setNavs((prev) => {
      const updatedNav = [...prev];
      updatedNav.map((map) => {
        map.isSelected = false;
        if (map.id === nav.id) map.isSelected = true;
      });
      return updatedNav;
    });
    props.onSelect(nav.target);
  };

  return (
    <>
      <div className={classes.nav}>
        <div className={classes.mainIcon}><img src={ryulabIcon} /></div>
        {navs.map((nav) => (
          <div
            className={`${classes.navItem} ${
              nav.isSelected ? classes.selected : ""
            }`}
            key={nav.id}
            onClick={clickHandler.bind(null, nav)}
          >
            {nav.name}
          </div>
        ))}
      </div>
    </>
  );
};

export default Nav;
